.copyright {
  box-sizing: border-box;
  font-family: $font-basic;
  font-weight: 400;
  font-style: normal;
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $basic-gable-green;
  background-color: $basic-alabaster;
  position: relative;
  padding: 14px 0;

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 1px;
    border-top: 1px solid #626962;
    opacity: 0.2;

    @media (max-width: $tablet-width-only) {
      top: -7px;
    }

    @media (max-width: $mobile-width-only) {
      top: 6px;
    }
  }
}

.copyright__wrapper {
  @include wrapper;
  padding: 0 20px 0 20px;

  @media (max-width: $tablet-width-only) {
    padding: 0 25px 0 26px;
  }

  @media (max-width: $mobile-width-only) {
    padding: 19px 16px 20px 17px;
  }
}

.copyright__list {
  @include reset-list;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin: 0 5.1% 0 4.9%;

  @media (max-width: $tablet-width-only) {
    @include reset-list;
  }

  @media (max-width: $mobile-width-only) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.copyright__item {
  opacity: 0.5;
  color: $basic-gable-green;

  &--01 {
    margin-right: 75px;
  }

  &--02 {
    margin-right: 142px;
  }

  &--03 {
    margin-right: auto;

    @media (max-width: $mobile-width-only) {
      margin: 0 0 22px 0;
    }
  }
}

.copyright__item--only-desktop {
  @media (max-width: $tablet-width-only) {
    display: none;
  }
}

.copyright__item--04 a {
  color: $basic-gable-green;

  &:hover {
    opacity: 0.5;
  }

  &:focus {
    opacity: 0.3;
  }
}
